body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.qualifier-conatiner {
  display: flex;
}
.qualifier-result {
  width: 160px;
  display: flex;
  flex-direction: column;
}
.result-label {
  flex: 1 1;
  border-bottom: 3px solid black;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.result-value {
  flex: 1 1;
}
.right-border {
  border-right: 3px solid black;
}
.group-item-container {
  display: flex;
  align-items: center;
  height: 100px;
}
.group-name {
  margin-left: 10px;
  font-size: 80px;
  width: 60px;
}
.disable-font {
  color: rgb(125,138,161);
}
.team-container {
  margin-left: 10px;
  width: 100px;
}
.team-name {
  overflow: hidden;
  text-overflow: ellipsis;
}
.group-result {
  height: 100%;
  width: 180px;
  color: black;
  margin-left: 10px;
  display: flex;
  flex-direction: column;
}
.reult-type, .group-winner {
  height: 50%;
}
.reult-label {
  border-bottom: black 3px solid;
  display: flex;
  align-items: flex-end;
  padding: 0 16px;
  flex: 1 1;
}
.right-border {
  border-right: 3px solid black;
}
.left-border {
  border-left: 3px solid black;
}
.result-value {
  text-align: center;
  padding-top: 4px;
  flex: 1 1;
}
.spinner {
  margin: 0 auto 0;
  width: 70px;
  text-align: center;
}
.spinner .bounce {
  width: 10px;
  height: 10px;
  _background-color: #03A9F4;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}
.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0) 
  }
  40% {
   -webkit-transform: scale(1.0) 
  }
}
@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    transform: scale(0);
  } 40% {
    transform: scale(1.0);
  }
}
.semi-final-conatiner {
  display: flex;
}
.result-conatiner {
  display: flex;
  flex-direction: column;
  width: 200px;
}

.final-container {
  display: flex;
}
.final-result-conatiner {
  display: flex;
  flex-direction: column;
  width: 600px;
  margin: 0 12px;
}
.reset-button {
  border-radius: 16px;
  border: 3px solid #ccc;
  height: 32px;
  margin-top: 20px;
}
.reset-button:focus {
  outline: 0;
}
.result-value {
  display: flex;
  flex-direction: column;
}
.results-conatiner {
  display: flex;
  flex: 1 1;
}
.semi-winner-conatiner {
  display: flex;
  flex-direction: column;
  width: 240px;
  flex: 1 1;
}
.champions-result {
  flex: 1 1;
  background-color: cornflowerblue;
}
.loosers-result {
  flex: 1 1;
  background-color: cyan;
}
.flex-box {
  flex: 1 1;
}
.result {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.right-border {
  border-right: 3px solid black;
}
